import { HashRouter, Switch, Route } from "react-router-dom";
import Home from "./components/home/Home";
import Page from "./components/page/Page";
import "./App.css";

function App() {
  return (
    <HashRouter>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/store" component={Page} />
      </Switch>
    </HashRouter>
  );
}

export default App;
